































































































import { Component, Vue } from 'vue-property-decorator'
const DataSet = require('@antv/data-set')

interface IBodrd{
  height: number,
  data: Array<object>,
  padding: object,
  scale: Array<object>
}
interface IPie{
  height: number,
  data: Array<object>,
  scale: Array<object>
}
@Component
export default class DashHome extends Vue {
  private char: any = null
  private line: any = null
  private pie: any = null
  private isShowSide: boolean = false
  private lineData: Array<object> = [
    { month: 'Jan', : 7.0, : 3.9, : 4.3, : 19 },
    { month: 'Feb', : 6.9, : 4.2, : 4.3, : 19 },
    { month: 'Mar', : 9.5, : 5.7, : 4.3, : 19 },
    { month: 'Apr', : 14.5, : 8.5, : 4.3, : 19 },
    { month: 'May', : 18.4, : 11.9, : 4.3, : 19 },
    { month: 'Jun', : 21.5, : 15.2, : 4.3, : 19 },
    { month: 'Jul', : 25.2, : 17.0, : 4.3, : 19 },
    { month: 'Aug', : 26.5, : 16.6, : 4.3, : 19 },
    { month: 'Sep', : 23.3, : 14.2, : 4.3, : 19 },
    { month: 'Oct', : 18.3, : 10.3, : 4.3, : 19 },
    { month: 'Nov', : 13.9, : 6.6, : 4.3, : 19 },
    { month: 'Dec', : 9.6, : 4.8, : 4.3, : 19 }
  ]
  private oneConfig: IBodrd = {
    height: 200,
    data: [],
    padding: { top: 20, right: 0, bottom: 60, left: 30 },
    scale: [{ dataKey: 'month', min: 0, max: 1 }]
  }

  private twoConfig: any = {
    height: 200,
    data: [],
    padding: { top: 20, right: 0, bottom: 60, left: 30 },
    scale: [{ dataKey: 'month', min: 0, max: 1 }],
    adjust: [{
      type: 'dodge',
      marginRatio: 1 / 32
    }]
  }

  private threePieonfig: any = {
    height: 210,
    width: 400,
    scale: [{
      dataKey: 'percent',
      min: 0,
      formatter: '.0%'
    }],
    pieStyle: {
      lineWidth: 1
    },
    padding: { top: 50, right: 20, bottom: 20, left: 10 },
    labelConfig: ['percent', {
      formatter: (val: any, item: any) => {
        return item.point.item + ': ' + val
      }
    }]
  }

  private oCRadioChang() {
    console.log(arguments, '')
    const data = [
      { name: '', 'Jan.': 18.1, 'Feb.': 28.5, 'Mar.': 39.1, 'Apr.': 81.4, 'May': 47, 'Jun.': 20.3, 'Jul.': 24, 'Aug.': 35.6 },
      { name: '', 'Jan.': 12.2, 'Feb.': 23.44, 'Mar.': 34.4, 'Apr.': 99.7, 'May': 52.6, 'Jun.': 35.5, 'Jul.': 37.4, 'Aug.': 42.4 }
    ]
    this.pointTwoBodrd<string>(data, ['Jan.', 'Feb.', 'Mar.', 'Apr.', 'May', 'Jun.', 'Jul.', 'Aug.'])
  }

  private pointOneBodrd<T>(data: Array<object>, rowKeys: Array<T>): void {
    const BodyDom: HTMLElement | null = document.getElementById('charts-body')
    this.oneConfig.height = BodyDom ? BodyDom.offsetHeight - 16 : 0
    this.char = new DataSet.View().source(data)
    this.char.transform({
      type: 'fold',
      fields: rowKeys,
      key: 'city',
      value: 'temperature'
    })
    this.oneConfig.data = this.char.rows
  }

  // 
  private pointTwoBodrd<T>(data: Array<object>, rowKeys: Array<T>): void {
    const BodyDom: HTMLElement | null = document.getElementById('charts-body')
    this.twoConfig.height = BodyDom ? BodyDom.offsetHeight - 16 : 0
    const sourceData = [
      { name: '', 'Jan.': 18.9, 'Feb.': 28.8, 'Mar.': 39.3, 'Apr.': 81.4, 'May': 47, 'Jun.': 20.3, 'Jul.': 24, 'Aug.': 35.6 },
      { name: '', 'Jan.': 12.4, 'Feb.': 23.2, 'Mar.': 34.5, 'Apr.': 99.7, 'May': 52.6, 'Jun.': 35.5, 'Jul.': 37.4, 'Aug.': 42.4 }
    ]
    this.line = new DataSet.View().source(sourceData)
    this.line.transform({
      type: 'fold',
      fields: rowKeys,
      key: '',
      value: ''
    })
    this.twoConfig.data = this.line.rows
  }

  // 
  pointThreeBodrdPie() {
    const BodyDom: HTMLElement | null = document.getElementById('charts-body')
    this.threePieonfig.height = BodyDom ? BodyDom.offsetHeight - 16 : 0
    const sourceData = [
      { item: '', count: 40 },
      { item: '', count: 21 },
      { item: '', count: 17 },
      { item: '', count: 13 },
      { item: '', count: 9 }
    ]
    this.pie = new DataSet.View().source(sourceData)
    this.pie.transform({
      type: 'percent',
      field: 'count',
      dimension: 'item',
      as: 'percent'
    })
    this.threePieonfig.data = this.pie.rows
  }

  mounted() {
    this.$nextTick(() => {
      this.pointOneBodrd<string>(this.lineData, ['', '', '', ''])
      this.pointTwoBodrd<string>([], ['Jan.', 'Feb.', 'Mar.', 'Apr.', 'May', 'Jun.', 'Jul.', 'Aug.'])
      this.pointThreeBodrdPie()
    })
  }
}
